* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html {
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

/* Let's get this party started */
::-webkit-scrollbar {
  width: 8px; }

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.9);
  border-radius: 8px; }

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #777;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.9); }
  ::-webkit-scrollbar-thumb:window-inactive {
    background-color: #777; }

body {
  font-family: 'ROBOTOCONDENSED-REGULAR';
  color: #002859;
  min-height: 100vh;
  overflow-x: hidden;
  display: flex;
  line-height: 1.5;
  font-size: 16px; }

main {
  position: relative;
  flex: 1 0 auto; }

a {
  transition: all 0.3s linear;
  text-decoration: none; }
  a:hover, a:visited, a:focus {
    outline: none !important;
    box-shadow: none;
    text-decoration: none; }

ul {
  margin: 0;
  padding: 0; }

.btn:hover,
.btn:focus {
  outline: 0;
  box-shadow: none !important;
  transition: all 0.3s linear; }

.pad-50 {
  padding: 50px 0; }

.padt-90 {
  padding-top: 90px; }

.mrt-90 {
  margin-top: 90px; }

.pd-0 {
  padding-bottom: 0 !important; }

.pt-0 {
  padding-top: 0 !important; }

.mt-0 {
  margin-top: 0 !important; }

.mb-5 {
  margin-bottom: 50px; }

.mt-1 {
  margin-bottom: 10px !important; }

.mr-10 {
  margin: 10px 0 !important; }

.page-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  min-height: 100vh;
  width: 100%;
  overflow: hidden; }

.main {
  z-index: 2;
  position: relative;
  padding-top: 118px; }

.qus {
  position: fixed;
  right: 15px;
  bottom: 90px; }
  .qus a {
    color: #000;
    font-size: 40px; }
    .qus a:hover {
      color: #FDD205; }

.hidden-large {
  display: none !important; }

footer {
  background-color: #182955;
  padding: 10px 0; }
  footer p {
    color: #808080;
    margin-bottom: 10px; }
  footer .social a {
    color: #ffffff;
    padding: 0 10px; }
    footer .social a:hover {
      color: #FDD205; }
  footer form {
    position: relative; }
    footer form .form-group {
      margin-bottom: 0; }
    footer form input.form-control {
      width: calc(100% - 90px);
      height: 44px;
      background-color: #fff;
      border: 1px solid #707070;
      color: #757575;
      display: inline-block;
      border-radius: 0;
      vertical-align: middle; }
      footer form input.form-control:focus {
        box-shadow: none;
        border-color: #FDD205; }
    footer form .btn {
      background-color: #FDD205;
      color: #002859;
      height: 44px;
      width: 85px;
      border-radius: 0;
      display: inline-block;
      vertical-align: middle; }

